import type { ThemeTypes } from '../use-theme';

/**
 * 
 * @returns {string} light | dark
 */
const getDefaultTheme = (): ThemeTypes['type'] => {
  let endTime: number | null = new Date().setHours(6, 0, 0, 0); // 6
  let startTime: number | null = new Date().setHours(18, 0, 0, 0); // 18
  let justNowTime: number | null = new Date().getTime(); // 
  let defaultTheme: ThemeTypes['type'] = 'light';

  if (justNowTime > endTime) {
    // 6
    if (justNowTime > startTime) {
      // 6
      defaultTheme = 'dark';
    } else {
      // 
      defaultTheme = 'light';
    }
  } else {
    // 
    defaultTheme = 'dark';
  }

  endTime = null;
  startTime = null;
  justNowTime = null;
  return defaultTheme;
};

export default getDefaultTheme;
